/*---- roboto ----*/
@font-face {
  font-family: "roboto";
  font-style: normal;
  font-weight: 100;
  src: url("../fonts/roboto/roboto-v18-latin-100.eot");
  /* IE9 Compat Modes */
  src: local("robotoThin"), local("roboto-Thin"), url("../fonts/roboto/roboto-v18-latin-100.eot?#iefix") format("embedded-opentype"), url("../fonts/roboto/roboto-v18-latin-100.woff2") format("woff2"), url("../fonts/roboto/roboto-v18-latin-100.woff") format("woff"), url("../fonts/roboto/roboto-v18-latin-100.ttf") format("truetype"), url("../fonts/roboto/roboto-v18-latin-100.svg#lato") format("svg");
  /* Legacy iOS */ }

@font-face {
  font-family: "roboto";
  font-style: italic;
  font-weight: 100;
  src: url("../fonts/roboto/roboto-v18-latin-100italic.eot");
  /* IE9 Compat Modes */
  src: local("roboto Thin Italic"), local("roboto-ThinItalic"), url("../fonts/roboto/roboto-v18-latin-100italic.eot?#iefix") format("embedded-opentype"), url("../fonts/roboto/roboto-v18-latin-100italic.woff2") format("woff2"), url("../fonts/roboto/roboto-v18-latin-100italic.woff") format("woff"), url("../fonts/roboto/roboto-v18-latin-100italic.ttf") format("truetype"), url("../fonts/roboto/roboto-v18-latin-100italic.svg#lato") format("svg");
